let compressRatio = 0.8, // ÂúñÁâáÂ£ìÁ∏ÆÊØî‰æã
    imgNewWidth = 254, // ÂúñÁâáÊñ∞ÂØ¨Â∫¶
    imgDom,
    canvas = document.createElement("canvas"),
    context = canvas.getContext("2d");
/**
 * File ËΩâ base64
 * @param {*} file 
 */
 export const toBase64 = file => new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = error => reject(error);
});
/**
 * ÂèñÂæóÂúñÁâáÂ§ßÂ∞è
 * @param {*} file 
 */
 export const getSize = (file) => new Promise((resolve, reject) => {
    var _URL = window.URL || window.webkitURL;
    var img = new Image();

    img.onload = () => resolve({ height: img.height, width: img.width });
    img.onerror = reject;

    img.src = _URL.createObjectURL(file);
    imgDom = img;
});
/**
 * Â£ìÁ∏ÆÂúñÁâá‰∏¶ÂõûÂÇ≥Â£ìÁ∏ÆÂæåÁöÑbase64Ëàásize
 * @param {*} file 
 * @param {*} sizeImage 
 * @param {*} imageUrl 
 */
 export const getCompressImage = (file, sizeImage, imageUrl) => new Promise((resolve, reject) => {
    var width = sizeImage.width, // ÂúñÁâáÂéüÂßãÂØ¨Â∫¶
        height = sizeImage.height, // ÂúñÁâáÂéüÂßãÈ´òÂ∫¶
        imgNewHeight = imgNewWidth * height / width, // ÂúñÁâáÊñ∞È´òÂ∫¶
        newImg;

    console.log("Ê™îÊ°àÂ§ßÂ∞èÁ¥Ñ " + Math.round(file.size / 1000));

    // ‰ΩøÁî® canvas Ë™øÊï¥ÂúñÁâáÂØ¨È´ò
    canvas.width = imgNewWidth;
    canvas.height = imgNewHeight;
    context.clearRect(0, 0, imgNewWidth, imgNewHeight);

    // Ë™øÊï¥ÂúñÁâáÂ∞∫ÂØ∏
    context.drawImage(imgDom, 0, 0, imgNewWidth, imgNewHeight);

    // È°ØÁ§∫Êñ∞ÂúñÁâá
    newImg = canvas.toDataURL("image/jpeg", compressRatio).split(",")[1];
    console.log("Ê™îÊ°àÂ§ßÂ∞èÁ¥Ñ " + Math.round(0.75 * newImg.length / 1000));
    resolve({ width, height, newImg, imgNewHeight, imgNewWidth });
});

